import React, { useState, useCallback, useEffect } from 'react';
import { useEditor, EditorContent } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Image from '@tiptap/extension-image';
import { Editor } from '@tiptap/core';
import styles from '../styles/TiptapEditor.module.css';
import { uploadImage } from '@/api/blogService/blog';


// Props íƒ€ì… ì •ì˜
interface TiptapEditorProps {
  content: string;
  onChange: (content: string) => void;
  // onThumbnailChange prop ì œê±°ë¨
}

// ê³µë°± ë³´ì¡´ ì²˜ë¦¬ í•¨ìˆ˜
function preserveSpacesInHTML(html: string): string {
  return html
  .replace(/<p><\/p>/g, '<p>&nbsp;</p>')
  .replace(/<p>\s*<\/p>/g, '<p>&nbsp;</p>')
  .replace(/(<p[^>]*>)(\s+)/g, (match, tag, spaces) => {
    return tag + '&nbsp;'.repeat(spaces.length)
  })
  .replace(/(\s{2,})/g, (match) => {
    return '&nbsp;'.repeat(match.length)
  })
}

// ----------------------------------------------------------------------
// MenuBar ì»´í¬ë„ŒíŠ¸
// ----------------------------------------------------------------------

const MenuBar = ({ editor }: { editor: Editor | null }) => {
  if (!editor) return null

  // ì´ë¯¸ì§€ ì—…ë¡œë“œ í•¸ë“¤ëŸ¬
  const addImage = useCallback(() => {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = 'image/*';
    input.onchange = async (event) => {
      const target = event.target as HTMLInputElement;
      const file = target.files?.[0];
      if (file) {
        try {
          // íŒŒì¼ ì—…ë¡œë“œ API í˜¸ì¶œ
          const result = await uploadImage(file);

          // âœ… [ì˜¤ë¥˜ ìˆ˜ì •] result ê°ì²´ì—ì„œ .url ì†ì„±ì„ ì§ì ‘ ê°€ì ¸ì˜µë‹ˆë‹¤.
          // (API íƒ€ì… ì •ì˜ì— ë”°ë¼ .data.imageUrl ëŒ€ì‹  .url ì‚¬ìš©)
          const url = (result as any).url;

          if (url) {
            // ì—…ë¡œë“œ ì„±ê³µ ì‹œ ì—ë””í„°ì— ì´ë¯¸ì§€ ì‚½ì…
            editor.chain().focus().setImage({ src: url }).run();
          }
        } catch (error) {
          console.error("ì´ë¯¸ì§€ ì—…ë¡œë“œ ì‹¤íŒ¨:", error);
          alert("ì´ë¯¸ì§€ ì—…ë¡œë“œì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.");
        }
      }
    };
    input.click();
  }, [editor]);

  return (
      <div className={styles.menuBar}>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleHeading({ level: 1 }).run()}
            className={editor.isActive('heading', { level: 1 }) ? styles.isActive : ''}
        >
          H1
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleHeading({ level: 2 }).run()}
            className={editor.isActive('heading', { level: 2 }) ? styles.isActive : ''}
        >
          H2
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleHeading({ level: 3 }).run()}
            className={editor.isActive('heading', { level: 3 }) ? styles.isActive : ''}
        >
          H3
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleBold().run()}
            disabled={!editor.can().chain().focus().toggleBold().run()}
            className={editor.isActive('bold') ? styles.isActive : ''}
        >
          Bold
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleItalic().run()}
            disabled={!editor.can().chain().focus().toggleItalic().run()}
            className={editor.isActive('italic') ? styles.isActive : ''}
        >
          Italic
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleStrike().run()}
            disabled={!editor.can().chain().focus().toggleStrike().run()}
            className={editor.isActive('strike') ? styles.isActive : ''}
        >
          Strike
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleCode().run()}
            disabled={!editor.can().chain().focus().toggleCode().run()}
            className={editor.isActive('code') ? styles.isActive : ''}
        >
          Code
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleBulletList().run()}
            className={editor.isActive('bulletList') ? styles.isActive : ''}
        >
          List
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleOrderedList().run()}
            className={editor.isActive('orderedList') ? styles.isActive : ''}
        >
          Ordered List
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleBlockquote().run()}
            className={editor.isActive('blockquote') ? styles.isActive : ''}
        >
          Quote
        </button>
        <button
            type="button"
            onClick={addImage}
            className={editor.isActive('image') ? styles.isActive : ''}
        >
          Image
        </button>
        <button
            type="button"
            onClick={() => editor.chain().focus().toggleCodeBlock().run()}
            className={editor.isActive('codeBlock') ? styles.isActive : ''}
        >
          Code Block
        </button>
      </div>
  )
}

// ----------------------------------------------------------------------
// TiptapEditor ì»´í¬ë„ŒíŠ¸
// ----------------------------------------------------------------------

const TiptapEditor: React.FC<TiptapEditorProps> = ({ content, onChange }) => {
  const editor = useEditor({
    extensions: [
      StarterKit.configure({
        // whiteSpace ì„¤ì •
        paragraph: {
          HTMLAttributes: {
            style: 'white-space: pre-wrap;',
          },
        },
        // hardBreak í™œì„±í™” - Enterë¡œ ì¤„ë°”ê¿ˆ (ê¸°ë³¸ ë™ì‘)
        hardBreak: {
          keepMarks: false,
        },
      }),
      Image,
    ],
    content,
    immediatelyRender: false,
    onUpdate: ({ editor }) => {
      // HTML ì¶œë ¥ ì‹œ ê³µë°±ì„ ë³´ì¡´í•˜ë„ë¡ ì²˜ë¦¬
      const html = editor.getHTML()
      const preservedHtml = preserveSpacesInHTML(html)
      onChange(preservedHtml)
    },
    editorProps: {
      attributes: {
        style: 'white-space: pre-wrap;', // ê³µë°± ë³´ì¡´
      },
    },
    parseOptions: {
      preserveWhitespace: 'full',
    },
  })

  // ì´ˆê¸° content ì„¤ì • (props ë³€ê²½ ì‹œ ë‚´ë¶€ ì—ë””í„° ì—…ë°ì´íŠ¸)
  useEffect(() => {
    if (editor && editor.getHTML() !== content) {
      // âœ… [ì˜¤ë¥˜ ìˆ˜ì •] boolean ëŒ€ì‹  { emitUpdate: false } ê°ì²´ë¥¼ ì‚¬ìš©í•©ë‹ˆë‹¤.
      editor.commands.setContent(content, { emitUpdate: false });
    }
  }, [editor, content]);

  return (
      <div className={styles.editorWrapper}>
        <div className={styles.editorPane}>
          <MenuBar editor={editor} />
          <EditorContent editor={editor} className={styles.editorContent} />
        </div>

        <div className={styles.previewPane}>
          <h3 className={styles.previewTitle}>ë¯¸ë¦¬ë³´ê¸°</h3>
          <div
              className={styles.previewContent}
              style={{ whiteSpace: 'pre-wrap' }}
              dangerouslySetInnerHTML={{
                // ë„ ì²´í¬
                __html: (editor?.getHTML() || '')
                .replace(/<p><\/p>/g, '<p>&nbsp;</p>') // ë¹ˆ p íƒœê·¸ì— ê³µë°± ë¬¸ì ì¶”ê°€
              }}
          />
        </div>
      </div>
  )
}

export default TiptapEditor;